$color-white: #ffffff;
$color-cyan: #4cb6cb;
$color-grayHight: #788188;
$color-black: #262626;
$color-blackLow: #232c32;
$color-green: #1ab667;
$color-violet: #232c32;
$color-gray: #e8eff0;
$color-red: #f05050;
$color-text: #333;
$color-nav-color: #232c32;

.control {
    position: absolute;
    top: auto;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: #5a6a7a;
    .control-container {
        height: 100%;
        width: 100%;
        align-items: center;
        padding: 0 20px;
        position: relative;
        .item {
            height: 100%;
            display: flex;
            align-items: center;
            width: 35px;
            font-size: 2rem;
            .control-button {
                padding: 0;
                margin-top: 5px;
                width: 100%;
                color: $color-white;
            }
        }
        .item.listBtn {
            margin-left: 5px;
            font-size: 1.6rem;
        }

        .item.btn-play {
            font-size: 2.6rem;
        }
        .item.time {
            font-size: 1.2rem;
            color: $color-gray;
        }
        .item.btn-mute,
        .item.btn-random,
        .item.btn-repeat {
            font-size: 1.5rem;
        }

        .item.btn-range {
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 0 30px;
            margin: 0 20px;
            flex: 1;
            background-color: #4d5d6e;
            .name-singer {
                padding: 10px 0;
                font-size: 1.5rem;
                color: $color-white;
            }
        }
        .playerList {
            display: none;
            position: absolute;
            bottom: 62px;
            left: 192px;
            background-color: #4b5b6b;
            width: 360px;
            z-index: 10000000000000;
            ul {
                padding: 0;
                margin: 0;
                overflow-y: auto;
                max-height: calc(45px * 5);
            }
            ul::-webkit-scrollbar {
                width: 5px;
            }
            ul::-webkit-scrollbar-thumb {
                background: #999999;
            }
            ul::-webkit-scrollbar-track {
                background: rgb(255, 255, 255);
            }

            li {
                cursor: pointer;
            }
            li + li {
                border-top: 1px solid $color-grayHight;
            }

            .notting {
                width: 100%;
                height: 45px;
                line-height: 42px;
                padding: 0 15px;
                display: block;
                align-items: center;
                color: $color-white;
                font-size: 1.5rem;
            }

            .playerSong {
                width: 100%;
                height: 45px;
                display: flex;
                align-items: center;
                color: $color-grayHight;
                padding: 0 8px;

                span {
                    display: block;
                    height: 45px;
                    line-height: 45px;
                }

                .btnMyPlay {
                    font-size: 1rem;
                    width: 30px;
                    text-align: center;
                    span {
                        line-height: 46px;
                    }
                    .play {
                        color: $color-white;
                        font-size: 1.5rem;
                    }
                }

                .song-name {
                    font-size: 1.3rem;
                    font-weight: 500;
                    color: $color-grayHight;
                    flex: 1;

                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    user-select: none;
                    strong {
                        font-size: 1.5rem;
                        font-weight: 400;
                        color: $color-gray;
                    }
                }

                .remove {
                    font-size: 2rem;
                    width: 30px;
                    text-align: center;
                    span {
                        line-height: 50px;
                    }
                }
            }
        }
        .playerList.active {
            display: block;
        }
    }
}
