// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
    header {
        display: none;
    }
    .header-mobile {
        display: flex !important;
    }
    .wapper {
        height: unset;
        flex-direction: column;
        padding-top: 50px;
        .nav-container {
            display: none;
        }
        .friends {
            width: 100%;
            padding: unset;
            ul {
                margin-top: 0;
            }
        }
    }
    .absolute-md {
        position: unset !important;
    }
    .box-product {
        padding: 0 15px !important;
    }

    .control {
        .control-container {
            justify-content: space-around;
            .item.btn-range {
                display: none;
            }

            .playerList {
                width: 100%;
                left: 0;
                right: 0;
                background-color: transparent;
                padding-left: 15px;
                padding-right: 20px;
                ul {
                    background-color: #4b5b6b;
                }
            }
        }
    }
}

