$color-white: #ffffff;
$color-cyan: #4cb6cb;
$color-grayHight: #788188;
$color-black: #262626;
$color-blackLow: #232c32;
$color-green: #1ab667;
$color-violet: #232c32;
$color-gray: #e8eff0;
$color-red: #f05050;
$color-text: #333;
$color-nav-color: #232c32;
$color-yellow: #fad733;

.friends {
    position: relative;
    width: 240px;
    height: 100%;
    padding-top: 20px;
    border-spacing: 0;
    overflow: hidden;

    background-color: #e0eaec;
    ul {
        overflow-y: scroll;
        margin-bottom: 0;
        .connected {
            color: $color-grayHight;
            font-size: 1.8rem;
            font-weight: 400;
            padding: 10px 0;
            display: block;
        }

        position: absolute;
        width: 100%;
        bottom: 60px;
        left: 0;
        top: 0;

        padding: 0 15px;
        z-index: 1;
        .friend {
            display: flex;
            padding: 10px 0;
            .img {
                position: relative;
                img {
                    width: 35px;
                    height: 35px;
                    border-radius: 50%;
                }
                .status {
                    position: absolute;
                    width: 11px;
                    height: 11px;
                    background-color: $color-white;

                    border-radius: 50%;
                    border: 2px solid $color-white;
                    top: 0;
                    right: 0;
                    transform: translateX(15%);
                }
                .status.Error {
                    background-color: $color-red;
                }
                .status.Warning {
                    background-color: $color-yellow;
                }
                .status.Success {
                    background-color: $color-green;
                }
            }
            .info {
                padding: 0 10px;
                display: flex;
                flex-direction: column;
                text-transform: capitalize;
                .name {
                    font-size: 1.4rem;
                    color: $color-text;
                    text-align: left;
                }
                .from {
                    font-size: 1.2rem;
                    color: $color-grayHight;
                }
            }
        }
    }

    ul::-webkit-scrollbar {
        display: none;
        width: 5px;
    }

    /* Track */
    ul::-webkit-scrollbar-track {
        background: #eeeeee;
    }

    /* Handle */
    ul::-webkit-scrollbar-thumb {
        background: rgb(180, 180, 180);
        border-radius: 10px;
    }

    /* Handle on hover */
    ul::-webkit-scrollbar-thumb:hover {
        background: rgb(119, 119, 119);
    }

    ul::-webkit-scrollbar-button {
        width: 10px;
        height: 10px;
        background-color: rgba(50, 50, 50, 0.05);
    }
    .search {
        position: absolute;
        top: auto;
        width: 100%;
        bottom: 0;
        left: 0;
        overflow: hidden;
        padding: 15px 10px;

        display: flex;
        height: 60px;
        background-color: #242d33;
        z-index: 10000;
        .button {
            font-size: 1.6rem;
            font-weight: 1000;
            color: #627b8c;
            margin-top: 5px;
        }
        .input-search {
            background-color: transparent;
            border: none;
            outline: none;
            border-bottom: 1px solid #627b8c;
            color: $color-white;
            font-size: 1.1rem;
            width: 100%;
        }
    }
}

.friends:hover {
    ul::-webkit-scrollbar {
        display: unset;
    }
}
