$color-white: #ffffff;
$color-cyan: #4cb6cb;
$color-grayHight: #788188;
$color-black: #262626;
$color-blackLow: #232c32;
$color-green: #1ab667;
$color-violet: #232c32;
$color-gray: #e8eff0;
$color-red: #f05050;
$color-text: #333;
$color-nav-color: #232c32;
$color-yellow: #fad733;

.header-mobile {
    display: none;
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    padding: 0 15px;

    align-items: center;
    justify-content: space-between;
    background-color: $color-cyan;
    color: $color-white;
    z-index: 100000;
    font-size: 1.6rem;
    .btn-list {
        height: 100%;
    }
    .home {
        display: flex;
        align-items: center;
        height: 100%;
        font-size: 1.8rem;
        img {
            width: 40px;
            height: 40px;
        }
        span {
            padding: 0 10px;
        }
    }
    .setting {
        position: relative;
        height: 100%;
        .btn-setting {
            height: 100%;
            display: block;
            font-size: 1.8rem;
            span {
                height: 50px;
                line-height: 50px;
            }
        }
        input {
            display: none;
        }
        input[type="checkbox"]:checked ~ .profile-mobile {
            display: block !important;
        }
        .profile-mobile {
            padding-top: 25px;
            display: none;
            position: fixed;
            top: 50px;
            left: 0;
            width: 100%;
            background-color: $color-white;
            .profile {
                display: flex;
                flex-direction: column;
                height: unset;
                padding: 0 10px;
                .profile-container {
                    width: 100%;
                    flex-direction: column-reverse;
                    align-items: center;
                    .avatar {
                        width: 150px;
                        height: 150px;
                    }
                }
                .box {
                    display: block;
                    position: relative;
                    width: 100%;
                    margin-top: 15px;
                    margin-bottom: 15px;
                    border-radius: 3px;
                }
                a {
                    font-size: 1.3rem;
                }
            }
        }
    }
}
